import React, { Component } from 'react'
import Footer from "./footer"
import "slick-carousel/slick/slick.css"
import "slick-carousel/slick/slick-theme.css"
import resourse1 from"./images/resources-img_01.jpg"
import resourse2 from"./images/resources-img_02.jpg"
import resourse3 from"./images/resources-img_03.jpg"
import resourse4 from"./images/resources-img_04.jpg"
import resourse5 from"./images/resources-img_05.jpg"
import "./index.css"
import { Card } from 'react-bootstrap'
import Slider from "react-slick";
import ScrollToTop from './ScrollToTop'
export default class active extends Component {
    render() {
        var settings = {
          dots: true,
          infinite: true,
          speed: 200,
          arrow:true,
          autoplay:true,
          autoplaySpeedd:200,
          slidesToShow: 3,
          slidesToScroll: 3,
          responsive: [
            {
              breakpoint: 1000,
              settings: {
                slidesToShow: 2,
                slidesToScroll: 2,
                initialSlide: 2
              }
            },
            {
              breakpoint: 700,
              settings: {
                slidesToShow: 1,
                slidesToScroll: 1
              }
            }
          ]
        };
  return (
  <div>
  <div className="row">
  <div className="col-md-12">
  <h1>Activities</h1>
 </div>
 </div>
<div className="container-fluid">
<div className="row">
<div className="col-md-8">
    <h2>Training</h2>
  <ol type="1"style={{textAlign:"justify"}} >
	<li><h4><b>Institutional Training</b></h4>
		<ol type="a">
			<li>Institutional training conducted at colleges and schools is the mainstay of NCC training and is
      conducted by Associate NCC Officers and Armed Forces personnel. The syllabus comprises Common 
      Subjects and Service Subjects in the ratio of 70:30. While the Senior Wing/Division training is for 
      three years and has 300 periods, the Junior Wing/Division training is for two years and encompasses
      240 periods of training. The training year is from 01 April to 31 March. Training schedules planned
      for cadets ensure that the optimum benefits of the organisation reach maximum number of cadets. 
      Emphasis is on practical training. Case study method of instruction, wherever possible, is made
      to facilitate active participation and better assimilation.</li>

			<li>Institutional training includes basic military training to the cadets as part of the curriculum 
        and prepares them to join the Armed Forces.  It is conducted with the following specific purposes:-
				<ol type="i">
					<li>Firstly, to expose young cadets to a regimental way of life’ which is essential to inculcate
          in them the values of discipline, duty, punctuality, orderliness, smartness, respect for the 
          authorities, correct work ethos, and self- confidence.</li>

					<li>Secondly, to generate interest among the cadets by including and laying emphasis on those 
          aspects of Institutional Training which attract young cadets into the NCC and to provide them an 
          element of thrill and excitement.</li>

					<li>Thirdly, to inculcate Defence Services work ethos that are characterised by hard work, 
          sincerity of purpose, honesty, ideal of selfless service, dignity of labour, secular outlook, 
          comradeship, spirit of adventure and sportsmanship.</li>
				<br/>
        </ol>
</li>
</ol>                
</li>
<div style={{padding:"5px"}}>
<li><h4><b>Camp Training</b></h4>
<p>Camp training is the practical manifestation of institutional training. The basic aim of camps 
is to introduce the cadets to a regimented way of life and to help in developing camaraderie, team work, 
leadership qualities, self-confidence, self-reliance and dignity of labour in the minds of the cadets. The 
cadets are exposed to the excitement of camp life where they apply the theoretical knowledge that they had 
gained in institutional training. It is mandatory for Junior Division/ Junior Wing cadets to attend at least 
one camp and Senior Division/ Senior Wing cadets to attend a minimum of two camps during the period of their
 enrollment. NCC conducts over 1450 camps annually at an average frequency of more than 100 camps in a 
 month. Each NCC camp is structured for approximately 400 - 600 cadets with instructional staff 
 comprising three to four officers and 15-20 Associate NCC Officers (ANOs)/ Permanent Instructors 
 (PI) Staff/ Whole Time Lady Officers (WTLOs)/ Girl Cadet Instructors (GCIs). More than 8 lakh cadets 
 attend these camps each year. Republic Day Camp is the acme of NCC training and is conducted in January 
 every year. </p></li>
</div>
</ol>
</div>
<div className="col-md-4">
<h3>Scholarships</h3>
<br/>
<b>
<p>*Chiefs of Services Scholarships to the Best Cadets</p>
<p>*DG NCC Scholarship</p>
<p>*Cadet Welfare Society Scholarship</p>
<p>*Tamil Nadu State Government Scholarship</p>
<p>*Sahara Scholarship</p>
<p>*Scholarship from ANO</p>
</b>
<br/>
<h3><b>Important Activities</b></h3>
<br/>
<p><b>*Digital Forum</b></p>
<p><b>*NCC Company Day</b></p>
<p><b>*Azad Ki Amrit Mahatsov</b></p>
<p><b>*Army Day</b></p>
<p><b>*Flag Day</b></p>
<p><b>*Kargil Vijay Diwas</b></p>
<p><b>*Special Lectures on Patriotism</b></p>
<p><b>*Athletic Competitions</b></p>
<p><b>*Silent drill demonstration on Sports Day</b></p>
<p><b>*Local Trekking and Cycle Expedition</b> </p>
<br/>
<br/>
<br/>
</div>
</div>
</div>
<h2 align="center">CAREER OPPORTUNITIES FOR NCC CADETS IN ARMED FORCES</h2>
<div className="container-fluid bg-light"style={{width:"75%",padding:"5%",textAlign:"center"}} >
<Slider {...settings}>
<Card className="adjust" >     
<Card.Img src={resourse1} className="img-fluid" alt="resurces-img"></Card.Img>
<Card.Text>
<p style={{textAlign:"justify",padding:"4.2%"}}>Regular Officers in Indian <br/>  Army (IMA, Dehradun)</p>
</Card.Text>
<Card.Link href="https://www.indianarmy.nic.in" target="_blank">(www.indianarmy.nic.in).</Card.Link> 
</Card>
<Card className="adjust" >
<Card.Img src={resourse2} className="img-fluid" alt="resurces-img"></Card.Img>
<Card.Text>
<p style={{textAlign:"justify"}}>Short Service Commissioned Officers in Indian Army  (OTA, Chennai)</p>
</Card.Text>
<Card.Link href="https://www.indianarmy.nic.in" target="_blank">(www.indianarmy.nic.in).</Card.Link>
</Card>
<Card className="adjust">
<Card.Img src={resourse3} className="img-fluid" alt="resurces-img"></Card.Img>
<Card.Text>
<p style={{textAlign:"justify",padding:"1.3%"}}>Border Security Force (BSF)</p><br/>
</Card.Text>
<Card.Link href="https://www.bsf.nic.in" target="_blank">(www.bsf.nic.in). </Card.Link>
</Card>
<Card className="adjust">
<Card.Img src={resourse4} className="img-fluid" alt="resurces-img"></Card.Img>
<Card.Text>
<p style={{textAlign:"justify",padding:"3.6%"}}>Central Reserve Police Force (CRPF)</p>
</Card.Text>
<Card.Link href="https://www.crpf.nic.in" target="_blank">(www.crpf.nic.in).</Card.Link>
</Card>
<Card className="adjust">
<Card.Img src={resourse5} className="img-fluid" alt="resurces-img"></Card.Img>
<Card.Text>
<p>Indian Coast Guard</p><br/>
</Card.Text>
<Card.Link href="https://www.indiancoastguard.nic.in" target="_blank">(www.indiancoastguard.nic.in).</Card.Link>
</Card>
</Slider>
 </div>
 <br/>
 <br/>
 <hr/>
 <Footer />
 <ScrollToTop />
 </div>
  )
  }
}
