import React, { Component } from 'react'
import Footer from "./footer"
import orgimg from "./images/org.png"
import ScrollToTop from './ScrollToTop'
export default class org extends Component {
render() {
return (
<div>    
<div className="container-fluid">
<div className="row">
<div className="col-md-12">
<h1>Authorities</h1>
<h4><b>Director General	</b>			: Lt. Gen. <br/>
<b>Deputy Director General</b>			: Commodore <br/>
<b>Group Commander (Madurai)</b>		: Col. <br/>
<b>Commanding Officer - 7 (TN) Bn.</b>	: Lt. Col. 
</h4>
</div>
</div>
<hr/>
<div className="container">
<div className="row">
<div className="col-md-12">
<h2>NCC (ARMY – INFANTRY DIVISION), Sri Kaliswari College</h2>
</div>
<img src={orgimg} className="img-fluid" alt="Not Found" height="50%" />
<hr/>
</div>
</div>
</div>
<Footer />
<ScrollToTop />
</div>
);
}
}
