import React, { Component } from 'react';   
import "./index.css";
import about from "./about";
import Home from "./home";
import {Navbar,Nav} from "react-bootstrap";
import logo from "./images/logo.png";
import course from "./course"
import org from "./org"
import camp from "./camp"
import active from "./active"
import Photo from "./Photo"
import NotFound from './NotFound';
import { BrowserRouter as Router, Route, Link, Switch } from 'react-router-dom';   

class App extends Component {  
  render()
  {
    return(
	  <Router>  
  <Navbar variant="dark" bg="dark" expand="lg" className="text-center">
  <Navbar.Brand><img src={logo} className="img-fluid" alt="not fount"/></Navbar.Brand>
  <Navbar.Toggle aria-controls="basic-navbar-nav" />
  <Navbar.Collapse id="basic-navbar-nav">
    <Nav className="justify-content-end" style={{ width: "95%"}}> 
      <Nav.Link className="change" as={Link} to={"/"}> Home</Nav.Link>
      <Nav.Link className="change" as={Link} to={"/about"}> History</Nav.Link>
      <Nav.Link className="change" as={Link} to={"/org"}>Organisation</Nav.Link>
      <Nav.Link className="change" as={Link} to={"/active"}> Activities</Nav.Link>
      <Nav.Link className="change" as={Link} to={"/course"}> Course</Nav.Link>
      <Nav.Link className="change" as={Link} to={"/camp"}> Camp</Nav.Link>
      <Nav.Link className="change" as={Link} to={"/gallery"}> Gallery</Nav.Link>
    </Nav>
  </Navbar.Collapse>
</Navbar>
    <Switch>
      <Route exact path="/" component={Home} />  
      <Route path="/about" component={about} />  
      <Route path="/org" component={org} />  
      <Route path="/course" component={course} />  
      <Route path="/active" component={active} />  
      <Route path="/camp" component={camp} />  
      <Route path="/gallery" component={Photo} /> 
      <Route component={NotFound} />
    </Switch>  
  </Router> 
    );
  }
  }  
export default App;  